@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.tuyendung-ct > .container {
  padding-top: 50px;
  padding-bottom: 70px;
  background: #fff;
}
.tuyendung-ct .fw-700 {
  font-weight: 700;
}
.tuyendung-ct .title {
  color: #f26f21;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  margin-bottom: 20px;
}
.tuyendung-ct .new-attributes {
  display: flex;
  padding: 15px 0 0;
  width: 100%;
  font-family: "Muli";
  color: #333;
}
.tuyendung-ct .new-attributes .row {
  width: 100%;
}
.tuyendung-ct .new-attributes .attributes {
  display: flex;
  margin-bottom: 15px;
}
.tuyendung-ct .new-attributes .attr-title, .tuyendung-ct .new-attributes .attr-content {
  flex: 0 0 50%;
  max-width: 50%;
  font-size: 15px;
}
.tuyendung-ct .new-attributes .attr-title {
  font-weight: 700;
}
.tuyendung-ct p {
  color: #f26f21;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
  margin: 35px 0 20px;
  text-transform: uppercase;
}
.tuyendung-ct .flex-center {
  justify-content: flex-start;
}
.tuyendung-ct hr {
  margin: 0;
  border: 0.5px solid #e0e0e0;
}
.tuyendung-ct .main-content {
