}
.tuyendung-ct .main-content p, .tuyendung-ct .main-content strong, .tuyendung-ct .main-content span {
  font-family: Muli, sans-serif;
  font-size: 15px;
}
.tuyendung-ct .main-content li {
  display: flex;
  margin-bottom: 10px;
}
.tuyendung-ct .main-content li:before {
  color: #c5c5c5;
  margin-right: 10px;
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f9dd";
}
.tuyendung-ct .main-content li.strong {
  display: block;
}
.tuyendung-ct .main-content li.strong:before {
  content: none;
}
.tuyendung-ct .main-content li strong {
  margin-right: 5px;
}
.tuyendung-ct .btn-nop {
  color: #999;
  border: 1px solid #999;
  padding: 7px 65px;
  background: #ffffff;
  border-radius: 99px;
  position: relative;
  margin: 20px 0;
}
.tuyendung-ct .btn-nop span {
  text-transform: uppercase;
  font-size: 15px;
}
.tuyendung-ct .btn-nop:after {
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  border-radius: 100%;
  background: #f26f21;
  position: absolute;
  right: 3px;
  top: 50%;
  transform: translateY(-50%);
  padding: 8px;
}
.tuyendung-ct .vitri-khac .title {
  margin: 0;
  color: #fff;
  padding: 20px;
  background: #f26f21;
