}
.tuyendung-ct .vitri-khac .item {
  padding: 20px;
  padding-right: 10px;
  background: #f5f5f5;
}
.tuyendung-ct .vitri-khac .item:not(:last-child) {
  border-bottom: 1px solid #e1e1e1;
}
.tuyendung-ct .vitri-khac .item a {
  text-decoration: none;
}
.tuyendung-ct .vitri-khac .item p {
  color: #333;
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-size: 15px;
  margin: 0;
  text-transform: none;
}
.tuyendung-ct .vitri-khac .item p, .tuyendung-ct .vitri-khac .item time {
  font-family: Muli, sans-serif;
}
.tuyendung-ct .vitri-khac .item time {
