  font-size: 14px;
}
.tuyendung-ct iframe {
  width: 100%;
  max-width: 100%;
  border: 0;
  height: 755px;
}
@media (max-width: 1199.9px) {
  .tuyendung-ct iframe {
    height: 875px;
  }
}
@media (max-width: 576px) {
  .tuyendung-ct iframe {
    height: 835px;
  }
}
@media (max-width: 576px) {
  .tuyendung-ct > .container {
    padding-bottom: 50px;
  }
  .tuyendung-ct .title {
    font-size: 18px;
    line-height: 18px;
    margin-bottom: 16px;
  }
  .tuyendung-ct .table {
    margin: 5px 0;
